import { Component, OnInit, Output, EventEmitter } from '@angular/core';


@Component({
  selector: 'app-form',
  template: `<div>

  <input type="text" name="todo" [(ngModel)]="val" placeholder="Enter todo..." [(ngModel)]="todos"><br/><br/>
  <button type="button" (click)="passData()" >Add Todo</button>
  </div>`,
  styles: [`
  div{
    text-align:center;
  }

  input{
    width:400px;
    height:30px;
    border-style:none;
    border-bottom:1px solid white;
    font-size:23px;
    padding-left:5px;
    transition:border-bottom-color 0.3s 0s linear;
    font-family:"Times New Roman", Times, serif;
    background:transparent;
    color:white;

  }
  input:focus{
    outline-style:none;
    border-bottom-color:white;
  }
  ::placeholder{
    color:rgba(255,255,255,0.5);
  }
  button{
    background-color:transparent;
    padding:5px;
    border:1px solid white;
    border-radius:7px;
    transition:all 0.2s 0s linear;
    color:white;
    width:100px;
    height:30px;
  }

  button:hover{
    background-color:blue;
  }
  button:focus{
    outline-radius:7px;
  }`]
})
export class FormComponent implements OnInit {
  public todos;
  public val;


  @Output() childEvent = new EventEmitter();
  public passData() {
    if (this.val) {
    this.childEvent.emit(this.todos);
    }
  }
  constructor() { }

  ngOnInit() {

  }

}
