import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-root',
  template: `<div id="root">

  <div id="time">
  {{date|date:'shortTime'}}</div>
  <h1 id="greeting">Good Morning Mayank!!</h1>
  <h1 id="tag">What is your aim for today</h1>
  <app-form (childEvent)="addTodo($event)"></app-form>
  <div id="container" *ngFor="let x of todoSlab,index as i">
    <div id="slab">{{x}}<i class="fa fa-trash" (click)="removeTodo($event,i)"></i></div>

  </div></div>`,
  styles: [`
  div#time{
    color:white;
    font-size:70px;
    text-align:center;
  }
  h1#greeting{
    color:white;
    text-align:center;
    margin-top:10px;
    margin-bottom:100px;
  }
  h1#tag{
    color:white;
    text-align:center;
    font-family:New Century Schoolbook, serif;
    font-size:25px;
  }
  #root{
    min-height:100vh;
    background-image:url("https://wallpaper-house.com/data/out/9/wallpaper2you_354517.jpg");
    background-repeat:no-repeat;
    background-size:cover;
    padding-top:100px;
    box-sizing:border-box;
  }

  i{
    float:right;
  }
  div#slab{
    padding:5px;
    font-size:20px;
    width:400px;
    margin:auto;
    margin-top:15px;
    box-shadow:0 0 2px white;
    border-radius:2px;
    border-left:3px solid red;
    color:white;
  }`]
})
export class AppComponent implements OnInit {
  public todoSlab: any[] = [];
  public date = new Date();
  public addTodo(e) {
    console.log(this.todoSlab);
    this.todoSlab.push(e);
    localStorage.setItem('todo' , JSON.stringify(this.todoSlab));
  }
  public removeTodo(e,i) {
    this.todoSlab.splice(i,1);
    localStorage.setItem('todo' , JSON.stringify(this.todoSlab));
  }
  ngOnInit(){
    let x;
    this.todoSlab=JSON.parse(localStorage.getItem('todo')!=null?localStorage.getItem('todo'):'[]');
    setInterval(() => {
      this.date = new Date();
      x=this.date.getHours();
      if(x >= 18 || (x >= 0 && x<4)){

        (document.querySelector("h1") as HTMLElement).innerHTML="Good Night Mayank!!";
        (document.querySelector('#root') as HTMLElement)
        .style.background = "url('https://wallpaper-house.com/data/out/9/wallpaper2you_354547.jpg')" ;
      }
      else if(x>=4 && x<11){
        (document.querySelector("h1") as HTMLElement).innerHTML="Good Morning Mayank!!";
        (document.querySelector('#root') as HTMLElement)
        .style.background = "url('https://images.pexels.com/photos/592077/pexels-photo-592077.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=750&w=1260')" ;
      }
      else if(x>=11 && x<16){
        (document.querySelector("h1") as HTMLElement).innerHTML="Good Afternoon Mayank!!";
        (document.querySelector('#root') as HTMLElement)
        .style.background = "url('https://wallpaperstudio10.com/static/wpdb/wallpapers/3840x2160/211507.jpg')" ;
      }
      else{
        (document.querySelector("h1") as HTMLElement).innerHTML="Good Evening Mayank!!";
      }
    }, 1 );
  }
}
